<template>
  <div class="asset-table">
    <ul class="table-head">
      <li class="col first">{{ $t('assets.bottom.table.head.text1') }}</li>
      <li class="col">{{ $t('assets.bottom.table.head.text2') }}</li>
      <li class="col">{{ $t('assets.bottom.table.head.text3') }}</li>
      <li class="col">{{ $t('assets.bottom.table.head.text4') }}</li>
      <li class="col last">{{ $t('assets.bottom.table.head.text5') }}</li>
    </ul>
    <div class="table-body" v-if="data.length !== 0">
      <div class="row" v-for="(item, index) in data" :key="item.currency_id">
        <ul class="item">
          <li class="grid first">{{ item.currency_mark }}</li>
          <li class="grid">{{ item.count }}</li>
          <li class="grid">{{ item.num }}</li>
          <li class="grid">{{ item.forzen_num }}</li>
          <li class="grid last">
            <el-button type="text" @click="handleAction(index, 1)">{{ $t('assets.bottom.table.row.text1') }}</el-button>
            <el-button type="text" @click="handleAction(index, 2)">{{ $t('assets.bottom.table.row.text2') }}</el-button>
            <el-button type="text" @click="handleAction(index, 3)">{{ $t('assets.bottom.table.row.text3') }}</el-button>
            <el-button type="text" @click="handleToTransDetail(item)">{{ $t('assets.bottom.table.row.text4') }}</el-button>
            <el-dropdown class="ml8">
              <span class="el-dropdown-link">
                <el-button type="text">{{ $t('assets.bottom.table.row.text5') }}</el-button>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item
                  v-for="itemSub in item.couple_info"
                  :key="itemSub.trade_area_id"
                  @click.native="handleToTransaction(itemSub)">{{ itemSub.couple_name }}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </li>
        </ul>
        <component
          class="slots" 
          :is="currentComponent" 
          v-if="currentRow === index"
          :id="item.currency_id"
          :currency="item.currency_mark"
          :number="item.num"
          :leastNum="item.least_recharge_num"
          :type="'ctc'"
          @refresh="handleRefresh"></component>
      </div>
    </div>
    <div class="no-result" v-else>
      {{ $t('assets.bottom.noresult') }}
    </div>
  </div>
</template>

<script>
  import { userInfo } from '@/api/user'
  import Action1 from './Action1'
  import Action2 from './Action2'
  import Action3 from './Action3'
  export default {
    name: 'asset-table',
    components: {
      Action1,
      Action2,
      Action3
    },
    props: {
      data: {
        type: Array,
        default() {
          return []
        }
      },
      currentSwitch: {
        type: Number
      }
    },
    computed: {
      currentComponent() {
        const result = this.currentAction === 2
          ? Action2 : this.currentAction > 2
            ? Action3 : Action1
        return result
      }
    },
    watch: {
      currentSwitch(val) {
        this.currentRow = null
      }
    },
    data() {
      return {
        currentRow: null,
        currentAction: 1,
        userInfo: {}
      }
    },
    methods: {
      handleAction(index, action) {
        userInfo().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.userInfo = res.data
          if (this.userInfo.status !== 1) {
            this.$message({
              message: this.$t('translateLang.tradeDetail.text71'),
              duration: 1500,
              type: 'error'
            })
            return false
          }
          if ((this.userInfo.is_pwdtrade_exists).toString() !== '1') {
            this.$message({
              message: this.$t('translateLang.tradeDetail.text72'),
              duration: 1500,
              type: 'error'
            })
            return false
          }
          this.currentRow = index
          this.currentAction = action
        }).catch(() => {
        })
      },
      handleRefresh(val) {
        this.$emit('refresh', val)
      },
      handleToTransDetail(item) {
        this.$router.push({
          path: '/assets/transdetail',
          query: {
            currency_id: item.currency_id,
            currency_mark: item.currency_mark,
            type: 'ctc'
          }
        })
      },
      handleToTransaction(item) {
        this.$router.push({
          path: '/tradeDetail',
          query: {
            trade_area_id: item.trade_area_id,
            currency_id: item.currency_id,
            trade_currency_name: item.trade_currency_name,
            trade_currency_mark: item.trade_currency_mark
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .asset-table {
    .table-head{
      width: 100%;
      font-size: 0px;
      background-color: #f8f8f8;
      .col{
        display: inline-block;
        vertical-align: middle;
        width: 217.5px;
        color: #7b7e7f;
        font-size: 13px;
        padding: 12px 0;
      }
      .col.first{
        text-indent: 7px;
      }
      .col.last{
        width: 270px;
      }
    }
    .table-body{
      .row{
        border-bottom: 1px solid #f0f0f0;
        .item{
          width: 100%;
          font-size: 0px;
          .grid{
            display: inline-block;
            vertical-align: middle;
            width: 217.5px;
            font-size: 13px;
            padding: 24px 0;
            line-height: 1;
            word-break: break-all;
          }
          .grid.first{
            text-indent: 7px;
          }
          .grid.last{
            width: 270px;
            font-size: 0px;
            .el-button.el-button--text{
              border: 0;
              padding: 0px;
            }
            .el-button+.el-button{
              margin-left: 8px;
            }
            .ml8{
              margin-left: 8px;
            }
          }
        }
      }
      .row:last-child{
        border: 0;
      }
      .slots{
        background-color: #fafbff;
        padding: 20px 10px;
      }
    }
    .no-result{
      text-align: center;
      font-size: 14px;
      padding: 20px;
      color: #3f4041;
    }
  }
</style>



